@import '../../../assets/css/variable.less';

* {
  box-sizing: border-box;
}

.trading {
  display: flex;
  // height: 746px;

  .trading-left {
    width: 641px;
    border-right: 1px solid @divider_color;

    .top-ponts-pnl {
      padding: 39px 71px 23px 56px;
      .score-hover{
        .text{
          height: 35px;
        }
      }
      .tab-header {
        width: 514px;
        height: 64px;

        .active {
          border-radius: 4px;
        }
      }

      .ponts-pnl-top-ten {
        margin-top: 35px;

        .list-box {

          .address,
          .avgcoeff,
          .pnl {
            width: 100px;
            text-align: center;
          }

          .score {
            width: 92px;
            text-align: center;
          }
         

          .no {
            width: 14px;
          }

          .list-box-title {
            display: flex;
            height: 50px;
            line-height: 50px;
            justify-content: space-between;
          }

          .list-info {
            .list-info-box {
              color: #FFFFFF;
              display: flex;
              justify-content: space-between;
              align-items: center;
              height: 53px;
            }

            .top-three-no {
              color: #E27625;
            }

            .no {
              line-height: 34px;
            }
          }
        }
      }
    }
  }

  .trading-right {
    width: 608px;

    .your-estimated-rewards {
      height: 180px;
      border-bottom: 1px solid @divider_color;
      padding: 28px 33px 33px 33px;

      .your-estimated-rewards-title {
        display: flex;
        justify-content: space-between;
        color: #FFFFFF;
        font-size: 24px;

        .twitter-share {
          display: flex;
          align-items: center;
          cursor: pointer;
        }

        .share {
          color: #5577FD;
          font-size: 14px;
          margin-left: 5px;
          text-decoration: underline;
        }
      }

      .your-rewards-info {
        .your-deri {
          display: flex;
          align-items: center;
          margin-top: 25px;

          .deri-logo-bg {
            width: 52px;
            height: 52px;
            background-color: #5577FD;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;

            img {
              width: 21px;
              height: 25px;
            }
          }

          .span {
            margin-left: 15px;
            font-size: 32px;
            color: #FFFFFF;
          }
        }
      }


    }

    .your-score-fee-coeff {
      height: 191px;
      padding: 28px 33px 33px 33px;
      border-bottom: 1px solid @divider_color;

      .your-score-fee-coeff-title {
        color: #FFFFFF;
        font-size: 24px;
      }

      .your-score-fee-coeff-info {
        margin-top: 45px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .your-score,
        .your-fee,
        .your-coeff {
          font-size: 14px;

          .your-score-num,
          .your-fee-num,
          .your-coeff-num {
            font-size: 24px;
            color: @whiteFontColor;
          }
        }
      }
    }

    .raise-score {
      height: 174px;
      padding: 28px 33px 33px 33px;
      // padding: 28px 33px 33px 56px;
      border-bottom: 1px solid @divider_color;

      .raise-score-title-link {
        display: flex;
        justify-content: space-between;

        .raise-score-title {
          color: #FFFFFF;
          font-size: 24px;
        }

        .raise-score-link {
          display: flex;

          a {
            width: 90px;
            height: 31px;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: @second_color;
            text-decoration: none;
            color: #FFFFFF;
            border-radius: 20px;
            margin-left: 26px;
          }
        }
      }

      .raise-score-des {
        font-size: 14px;
        margin-top: 28px;

        .raise-hover {
          color: @second_color;
          display: inline-block;
          border-bottom: 1px solid @second_color;
          cursor: pointer;
          .text{
            border: 0;
          }
        }
      }
    }

    .your-contrib {
      margin-top: 20px;
      font-size: 12px;
      box-sizing: border-box;
      color: #CCCCCC;
      .total-score-box,
      .your-score-box,
      .your-point-box,
      .stage {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 50px;
        padding-right: 10px;
        .stage-title,
        .total-score,
        .your-score,
        .your-point {
          width: 80px;
          text-align: center;
        }
      }

      .total-score-box,
      .your-score-box,
      .your-point-box {
        height: 40px;
        padding-left: 10px;

      }

      .your-point-box {
        border-bottom: 0px;

        .point-hover {
          display: inline-block;
          cursor: pointer;
          // border-bottom: 1px dashed #FFF;
        }
      }
    }
  }
}
@media only screen and (max-width : 759px) {
  .trading{
    // height: 1408px;
    display: block;
    .trading-left{
      width: 375px;
      border: 0;
      border-bottom: 1px solid @divider_color;
      .top-ponts-pnl{
        padding: 32px 18px 23px 18px;
        font-size: 14px;
        .feespaid{
          display: none;
        }
        .tab-header{
          width: 339px;
        }
        .ponts-pnl-top-ten{
          .list-box{
            .address,.avgcoeff{
              width: 90px;
            }
            .list-info{
              .list-info-box{
                height: 40px;
              }
            }

          }
        }
      }
    }
    .trading-right{
      width: 375px;
      border-bottom: 1px solid @divider_color;
      .your-estimated-rewards{
        padding: 28px 18px 26px 18px;
        height: 164px;
        .your-rewards-info{
          .your-deri{
            margin-top: 27px;
          }
        }
      }
      .your-score-fee-coeff,.raise-score{
        padding: 28px 18px 0px 18px;
      }
      .raise-score{
        height: 252px;
        .raise-score-title-link{
          display: block;
          .raise-score-link{
            margin-top: 13px;
            .fist-a{
              margin-left: 0;
            }
          }
        }
        .raise-score-des{
          margin-top: 14px;
        }
      }
    }
  }
}
@media only screen and (min-width : 760px) and (max-width:1139px) {
  .trading{
    .trading-left{
      width: 372px;
      .top-ponts-pnl{
        padding: 29px 18px 22px 18px;
        font-size: 12px;
        .feespaid{
          display: none;
        }
        .tab-header{
          width: 336px;
          height: 68px;
        }
        .ponts-pnl-top-ten{
          .list-box{
            .address,.avgcoeff{
              width: 80px;
            }
            .list-info{
              .list-info-box{
                height: 56px;
              }
            }

          }
        }
      }
    }
    .trading-right{
      width: 388px;
      .your-estimated-rewards,.your-score-fee-coeff,.raise-score{
        padding: 22px 18px 27px 18px;
      }
      .raise-score{
        height: 204px;
        .raise-score-title-link{
          display: block;
          .raise-score-link{
            margin-top: 13px;
            .fist-a{
              margin-left: 0;
            }
          }
        }
        .raise-score-des{
          margin-top: 14px;
        }
      }
      .your-contrib{
        margin-top: 10px;
        .stage{
          height: 40px;
        }
      }
    }
  }
}
@media only screen and (min-width : 1140px) and (max-width:1270px) {
  .trading{
    .trading-left{
      width: 584px;
      .top-ponts-pnl{
        padding: 32px 69px 22px 53px;

      }
    }
    .trading-right{
      width: 551px;
    }
  }
}