@import '../../../assets/css/variable.less';

.trade-claim {
  width: 1142px;
  margin: 0 auto;
  margin-bottom: 51px;
  border: 1px solid @divider_color;
  background: rgba(85, 119, 253, 0.1);
  padding-left: 38px;
  padding-top: 17px;
  height: 192px;
  color: #FFFFFF;

  .rewards-claim {
    font-weight: normal;
    font-size: 24px;
  }

  .rewards-info {
    height: 64px;
    margin-top: 47px;

    .md-rewards-info {
      display: none;
    }

    .lg-rewards-info {
      width: 100%;
      display: flex;
      justify-content: space-between;
      flex-direction: row;
    }

    .my-rewards,
    .claimed-deri,
    .locked-deri,
    .claimable-deri,
    .cliam-button {
      flex: 1;
      margin-left: 54px;
    }

    .my-rewards,
    .claimed-deri,
    .locked-deri {
      border-right: 1px solid @divider_color;
    }

    .cliam-button {
      padding-right: 42px;
      display: flex;
      align-items: center;
    }

    .my-rewards {
      margin-left: 0;
    }

    .claim-title {
      font-weight: 500;
      font-size: 14px;
      height: 22px;
      color: @defaultFontColor;
    }

    .claim-num {
      font-weight: 500;
      font-size: 24px;
      height: 38px;
      white-space: nowrap;
      padding-right: 20px;
    }
  }
}

@media only screen and (max-width:759px) {
  .trade-claim {
    width: 327px;
    height: 332px;
    padding-left: 24px;
    .rewards-info {
      margin-top: 30px;

      .lg-rewards-info {
        // display: none;
        flex-wrap: wrap;

        .my-rewards,
        .claimed-deri,
        .locked-deri,
        .claimable-deri,
        .cliam-button {
          margin-left: 0;
          margin-top: 20px;
          margin-left: 20px;
        }
      }
    }
  }
}

@media only screen and (min-width : 760px) and (max-width:1139px) {
  .trade-claim {
    width: 725px;
    height: 219px;

    .rewards-info {
      margin-top: 30px;

      .lg-rewards-info {
        display: none;
      }

      .md-rewards-info {
        display: block;

        .top-rewards-info {
          display: flex;
          justify-content: space-between;

          .my-rewards,
          .cliam-button {
            flex: 0;
            border: 0;
          }
        }

        .bottom-rewards-info {
          display: flex;
          margin-top: 10px;

          .claimed-deri {
            margin-left: 0;

          }
        }
      }

    }

  }
}

@media only screen and (min-width : 1140px) and (max-width:1270px) {
  .trade-claim {
    width: 1041px;
  }
}