@import '../../../assets/css/variable.less';

* {
  box-sizing: border-box;
}

.staking {
  width: 100%;
  // height: 697px;

  .title-timeout {
    // height: 175px;
    padding: 42px 51px 36px 56px;

    .time-slider {
      background: rgba(255, 255, 255, 0.1);
      border-radius: 200px;
    }

    .title-time-text {

      .title-text,
      .start-end-time-epoch {
        display: flex;
        justify-content: space-between;
      }

      .title-text {
        font-size: 32px;
        color: #FFFFFF;
      }

      .start-end-time-epoch {
        margin-top: 8px;
        font-size: 14px;
      }

      .slider {
        height: 10px;
        // background-color: @second_color;
        margin-top: 28px;
      }

      .xs-show {
        display: none;
      }
    }
  }

  .rewards-box {
    padding-left: 56px;
    padding-right: 51px;
    display: flex;
    justify-content: space-between;

    .deri-text {
      font-size: 11px;
      color: @defaultFontColor;
      display: inline-block;
      width: 43px;
      height: 20px;
      border-radius: 100px;
      border: 1px solid rgba(224, 236, 255, 0.5);
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 17px;
      margin-left: 10px;
    }

    .total-rewards-pool {
      width: 579px;
      height: 240px;
      background: url("../img/rewards.png");
      background-repeat: no-repeat;
      padding-left: 37px;
      padding-top: 27px;

      .deri-logo-bg {
        width: 70px;
        height: 70px;
        background-color: @second_color;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      .total-rewards-num {
        margin-top: 36px;

        .total-rewards-pool-title {
          font-size: 14px;
        }

        .toatl-rewards-deri {
          font-size: 56px;
          color: #FFFFFF;
          display: flex;
          align-items: flex-start;
        }

      }
    }

    .open-lock-rewards {
      width: 552px;
      height: 239px;

      .top-open-lock-box,
      .bottom-open-lock-box {
        height: 117px;
        display: flex;
        justify-content: space-between;

        .lock-open {
          width: 271px;
          height: 117px;
          border: 1px dashed #222E40;
          opacity: 0.5;
          position: relative;
          padding-left: 20px;
          padding-top: 13px;
          cursor: pointer;

          .address-reward {
            display: flex;
            align-items: center;
            font-size: 32px;
            color: @whiteFontColor;
            margin-top: 10px;

            .deri-text {
              margin: 0;
              margin-left: 10px;
            }
          }

          .percentage-reward {
            margin-top: 6px;
          }

          .lock-logo {
            width: 54px;
            height: 29px;
            position: absolute;
            top: 0;
            right: 0;
            background-color: #112140;
            border-radius: 0 0 0 10px;
            text-align: center;
            line-height: 29px;
            color: #E27625;
          }
        }

        .open {
          border: 0;
          opacity: 1;
          background-image: url("../img/reward-open.png");
          background-repeat: no-repeat;

          .open-logo {
            background-color: @primary_color;
            color: #FFFFFF;
            top: 1px;
            right: 1px;
          }
        }
      }

      .bottom-open-lock-box {
        margin-top: 6px;
      }
    }
  }

  .stage-points {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 234px;
    margin-top: 49px;
    border-top: 1px solid @divider_color;
    border-bottom: 1px solid @divider_color;

    .stage-points-info {

      .top-stage-points,
      .bottom-stage-points {
        .points-box {
          text-align: center;

          .stage-box {
            width: 70px;
            height: 22px;
            margin: 0 auto;
            background-color: @primary_color;
            color: #FFFFFF;
            border-radius: 0 0 10px 10px;
          }

          .points-num {
            color: @whiteFontColor;
            font-size: 24px;
            width: 224px;
            height: 45px;
            white-space: nowrap;
            line-height: 45px;
            margin: 0 auto;
            margin-top: 16px;
          }

          .start-end-stage-time {
            font-size: 12px;
          }
        }
      }
    }
  }
}

@media only screen and (max-width : 759px) {
  .staking {
    // height: 1240px;

    .title-timeout {
      padding: 18px;

      .xs-hide {
        display: none;
      }

      .title-time-text {
        .title-text {
          display: block;
        }

        .silder {
          margin-top: 18px;
        }

        .xs-show {
          display: flex;
          justify-content: space-between;
          margin-top: 9px;
          font-size: 14px;

          .time {
            color: #FFFFFF;
          }
        }
      }
    }

    .rewards-box {
      padding-left: 18px;
      padding-right: 18px;
      display: block;

      .total-rewards-pool {
        width: 327px;
        height: 193px;
        padding-top: 21px;
        padding-left: 24px;
        background: url("../img/reward-xs.png");

        .total-rewards-num {
          margin-top: 10px;

          .toatl-rewards-deri {
            font-size: 42px;
          }
        }
      }

      .open-lock-rewards {
        width: 327px;
        height: 484px;
        display: block;
        margin-top: 8px;

        .top-open-lock-box,
        .bottom-open-lock-box {
          display: block;
          height: 242px;

          .lock-open {
            width: 327px;
            margin-top: 8px;
          }

          .open {
            background-image: url("../img/reward-open-xs.png");
          }
        }
      }
    }

    .stage-points {
      margin-top: 39px;
      height: 340px;

      .stage-points-info {
        height: 272px;
        border-top: 1px solid @divider_color;

        .top-stage-points,
        .bottom-stage-points {
          display: flex;
          border-bottom: 1px solid @divider_color;

          .points-box {
            width: 187px;
            height: 136px;
            background: rgba(85, 119, 253, 0.1);
            border-right: 1px solid @divider_color;

            .points-num {
              width: 146px;
            }
          }

          .xs-last {
            border: 0;
          }
        }
      }
    }
  }
}

